import request from "@/utils/request.js";

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function topics(params) {
  return request({
    // closeLoading: true,
    // closeInterceptors: true,
    url: "/v3/history", //url
    method: "get",
    params: params //post data: params
  });
}
