<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder=""
          size="small"
          style="width: 140px"
          v-model="searchParams.title"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.type"
          clearable
          style="width: 140px"
          placeholder=""
          size="small"
        >
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
        <el-button type="success" size="small" @click="refresh()"
          ></el-button
        >
        <el-button type="warning" size="small" @click="clearSearchParams()"
          ></el-button
        >
      </div>
    </ToolBar>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="title" label=""> </el-table-column>
      <el-table-column prop="details" label=""></el-table-column>
      <el-table-column prop="flight_number" label=""></el-table-column>
      <el-table-column label="" width="400" show-overflow-tooltip>
        <template slot-scope="scope">
          <a :href="scope.row.links.article" target="_blank" class="buttonText">{{scope.row.links.article}}</a>
        </template>
      </el-table-column>
      <el-table-column prop="event_date_utc" label=""></el-table-column>
      <el-table-column fixed="right"   label="">
        <div slot-scope="scope" >
          <el-button type="primary" size="small" @click="routeDemo(scope.row)"
            ><i class="el-icon-edit"></i><span>
            
          </span></el-button
          >
        </div>
      </el-table-column>
    </el-table>
       <Pagination
         :params="searchParams"
         :requestFunc="requestFunc"
         ref="pagination"
         @returnData="returnData"
       />
    <Edit :showEditDialog="showEditDialog" @close="showEditDialog = false" />
  </div>
</template>

<script>
import { topics } from "@/api/sys/history";
import { exportCvsTable } from "@/utils/cvs";
import { resetObject } from "@/utils/common";
import Edit from "./Edit.vue";
export default {
  data() {
    return {
      searchParams: {
        title: "",
        type: ""
      },
      showEditDialog: false,
      tableData: []
    };
  },
  // created() {
  //   topics()
  //     .then(r => {
  //       this.tableData = r;
  //     })
  //     .catch(() => {});
  // },
  methods: {
    requestFunc(param) {
      return topics(param);
    },
    routeDemo() {
      this.$message.info("");
    },
    exportTable() {
      exportCvsTable(
        [
          { title: "", field: "title" },
          { title: "", field: "visit_count" }
        ],
        this.tableData,
        ""
      );
    },
    refresh() {
      this.$refs.pagination.Refresh(); //
    },
    returnData(pageList) {
      this.tableData = pageList;
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    }
  },
  components: { Edit }
};
</script>
